<template>
    <div class="startup">
        <a-spin :spinning="loading">
            <div class="spin-content">

                <a-row>
                    <div class="mt30"></div>
                    <a-col :span="16" :offset="4" class="starting-project">
                        <a-col :span="12" class="t-left">Starting project</a-col>
                        <a-col :span="12" class="t-right">
                            <span @click="onClickBack" style="cursor:pointer">&lt;&lt; Back </span>
                        </a-col>
                    </a-col>

                    <a-col :span="16" :offset="4" class="bg-body">
                        <div class="mt30"></div>

                        <div v-if="isShow" class="t-left">
                            <a-row class="t-w">
                                <a-col class="m5" :span="12">
                                    <img class="img-r" alt="image" src="https://leth-ex.com/images/publish-01.jpg" width="100%" />
                                </a-col>
                                <a-col class="m5" :span="12">
                                    <div class="t1">{{this.itemData.symbol}}</div>
                                    <div>　　{{this.itemData.abstract}}</div>
                                </a-col>
                            </a-row>

                            <a-row class="t-w">
                                <a-col>
                                    <hr>
                                </a-col>
                                <a-col class="t-center t1">

                                    <a-row>
                                        <a-col :span="12" class="t-right">Countdown：</a-col>
                                        <a-col :span="12" class="t-left">
                                            <a-statistic-countdown title="" :value="deadline" format="YYYY-MM-DD HH:mm:ss:SSS" @finish="onFinishCountdown" :value-style="{ color: '#f00',fontSize:'2rem',fontFamily:'Courier New, Courier, monospace' }" />
                                        </a-col>
                                    </a-row>

                                    <a-row>
                                        <a-col :span="12" class="t-right">Start Time：</a-col>
                                        <a-col :span="12" class="t-left">{{itemData.start_time}}</a-col>
                                    </a-row>

                                    <a-row>
                                        <a-col :span="12" class="t-right">End Time：</a-col>
                                        <a-col :span="12" class="t-left">{{itemData.end_time}}</a-col>
                                    </a-row>

                                </a-col>
                                <a-col>
                                    <hr>
                                </a-col>
                                <a-col>
                                    <a-row type="flex" justify="center" align="top" class="t-center">
                                        <a-col :span="8" class="m5">
                                            <div style="width:100%">
                                                <p><strong>Coin Type：USDT</strong></p>
                                                <p>Exchange Ratio</p>
                                                <p>1 : {{ratio.usdt}}</p>
                                                <p>
                                                    <a-button @click="onClickShowParticipation('USDT')" :disabled="isDisabled" type="danger">Participation</a-button>
                                                </p>
                                            </div>
                                        </a-col>

                                        <a-col :span="8" class="m5">
                                            <div style="width:100%">
                                                <p><strong>Coin Type：ETH</strong></p>
                                                <p>Exchange Ratio</p>
                                                <p>1 : {{ratio.eth}}</p>
                                                <p>
                                                    <a-button @click="onClickShowParticipation('ETH')" :disabled="isDisabled" type="danger">Participation</a-button>
                                                </p>
                                            </div>
                                        </a-col>

                                        <a-col :span="8" class="m5">
                                            <div style="width:100%">
                                                <p><strong>Coin Type：BTC</strong></p>
                                                <p>Exchange Ratio</p>
                                                <p>1 : {{ratio.btc}}</p>
                                                <p>
                                                    <a-button @click="onClickShowParticipation('BTC')" :disabled="isDisabled" type="danger">Participation</a-button>
                                                </p>
                                            </div>
                                        </a-col>
                                    </a-row>

                                </a-col>
                                <a-col>
                                    <hr>
                                </a-col>
                            </a-row>

                            <a-row class="t-w">
                                <a-col class="m5" :span="24">
                                    <div class="t1">Introduction</div>
                                    <div>　　{{this.itemData.desc}}</div>
                                </a-col>
                            </a-row>
                        </div>

                    </a-col>

                </a-row>
                <a-row>
                    <div class="mt30"></div>
                </a-row>

            </div>
        </a-spin>
        <!-- modal -->
        <a-modal v-model="modal.visible" on-ok="onClickModalOk" title="Enter Participation" @ok="onClickModalOk" @cancel="onClickModalCancel" :keyboard="false" :maskClosable="false">
            <a-row>
                <a-col :span="12">Available Balance：<span class="t-red">{{currCoinBalance}} {{currCoinType}}</span></a-col>
                <a-col :span="12" class="t-right">
                    <a-button @click="onClickAllin" :disabled="loading_modal" type="link">Allin</a-button>
                </a-col>
            </a-row>
            <a-row>
                <a-col :span="11" class="t-right">{{currCoinType}}</a-col>
                <a-col :span="2" class="t-center">=></a-col>
                <a-col :span="11">{{symbol.toLocaleUpperCase()}}</a-col>
            </a-row>
            <a-row>
                <a-col :span="11" class="t-right">
                    <a-input v-model="currCoinAmount" :disabled="loading_modal" :placeholder="currCoinType" type="number" class="t-right" min="0" />
                </a-col>
                <a-col :span="2" class="t-center">≈</a-col>
                <a-col :span="11">
                    <a-input v-model="currPlatformCoinAmount" :placeholder="symbol.toLocaleUpperCase()" type="number" min="0" read-only />
                </a-col>
            </a-row>

            <template slot="footer">
                <a-button key="submit" type="primary" :loading="loading_modal" @click="onClickModalOk">
                    Submit
                </a-button>
            </template>
        </a-modal>

    </div>
</template>


<script>
export default {
    name: "Startup",
    components: {},
    data() {
        return {
            loading: false,
            loading_modal: false,
            isShow: false,
            symbol: '',
            itemData: {},
            walletList: [],
            modal: {
                visible: false,
            },

            // deadline: Date.now() + 1000 * 60 * 60 * 24 * 2 + 1000 * 30,
            deadline: Date.now(),

            isDisabled: false,

            //
            currCoinBalance: 0,
            currCoinType: '',
            currCoinAmount: 0,
            currPlatformCoinAmount: 0,

            ratio: {
                usdt: 0,
                eth: 0,
                btc: 0,
            },


            modalTemp: 0,

        };
    },
    watch: {
        currCoinAmount(val) {
            this.updatePlatformCoinAmount()
        }
    },
    destroyed: function () {
        clearTimeout(this.modalTemp)
    },
    methods: {
        onClickAllin() {
            this.currCoinAmount = this.currCoinBalance
            this.updatePlatformCoinAmount()
        },
        onClickBack() {
            this.$router.go(-1)
        },
        onClickModalOk() {
            if (this.currCoinAmount <= 0 || this.currPlatformCoinAmount <= 0) {
                this.$message.warning('The participation value cannot be zero!');
                return
            }
            this.loading_modal = true
            const body = {
                user_id: this.g.userInfo.id,
                // 从
                CoinAmount: this.currCoinAmount,
                CoinType: this.currCoinType,
                // 到
                PlatformCoinAmount: this.currPlatformCoinAmount,
                PlatformCoinType: this.symbol.toLocaleUpperCase()
            }
            $.post(`${this.g.apiUrl}/api/participation-currency-platform`, body, (res) => {
                if (res.flag != "ok") {
                    this.loading_modal = false
                    this.$message.error(res.flag)
                    return
                }

                clearTimeout(this.modalTemp)
                this.modalTemp = setTimeout(() => {
                    this.loading_modal = false
                    this.modal.visible = false
                    this.$message.success("Participate in the success！")

                    this.initCurrencyPlatformData()
                }, 1000)

            }).fail(err => {
                // console.error(err)
                this.loading_modal = false
                this.$message.error(err.statusText)
            })
            // 弹出另一种框 提示
            // this.$notification.open({
            //     message: 'TIPS',
            //     description: `${this.currCoinAmount} ${this.currCoinType} 兌換 ${this.currPlatformCoinAmount} ${this.symbol.toLocaleUpperCase()} 平臺幣`,
            //     icon: <a-icon type="smile" style="color: #108ee9" />,
            // });
        },
        onClickModalCancel(e) {
            this.loading_modal = false
        },
        onClickShowParticipation(coinType) {
            if (!this.g.userInfo) {
                this.$router.push('/login')
                return
            }
            this.currCoinAmount = 0
            this.currCoinType = coinType;
            this.currCoinBalance = this.getWallet(coinType).assets_amount
            this.currPlatformCoinAmount = 0
            this.modal.visible = true;
        },
        onFinishCountdown() {
            this.isDisabled = true
        },
        updatePlatformCoinAmount() {
            // console.log(this.currCoinBalance, this.currCoinAmount, this.currCoinBalance > this.currCoinAmount)
            if (this.currCoinBalance >= this.currCoinAmount) {
                this.currPlatformCoinAmount = this.g.round2(this.currCoinAmount * this.ratio[this.currCoinType.toLocaleLowerCase()], 8);
            } else {
                this.currCoinAmount = this.currCoinBalance
            }
        },
        getWallet(coinType) {
            const a = this.walletList
            for (let i = 0; i < a.length; i++) {
                const item = a[i]
                if (item.name.toLocaleLowerCase() == coinType.toLocaleLowerCase()) {
                    return item
                }
            }
        },
        getCoinRatio(arr, coinType) {
            for (let i = 0; i < arr.length; i++) {
                const item = arr[i]
                if (item.symbol == coinType.toLocaleLowerCase()) {
                    return item
                }
            }
            return null
        },
        initCurrencyPlatformData() {
            this.loading = true
            const url = `${this.g.apiUrl}/api/${this.symbol}/currency-platform`
            let query = {}
            if (this.g.userInfo) {
                query.user_id = this.g.userInfo.id
            }
            $.get(url, query, (res) => {
                this.loading = false
                if (res.flag != "ok") {
                    this.$message.error(res.flag)
                    return
                }
                // 平臺幣發布時間
                let o = res.data.currencyPlatform
                o.start_time = moment(o.start_time).format("YYYY-MM-DD HH:mm")
                o.end_time = moment(o.end_time).format("YYYY-MM-DD HH:mm")
                this.itemData = o
                this.isShow = true
                this.deadline = o.end_time
                if (new Date().getTime() > new Date(o.end_time).getTime()) {
                    this.isDisabled = true
                }
                // 我的錢包餘額
                this.walletList = res.data.walletList

                this.ratio.usdt = this.g.round2(1 / o.usdt_exchange, 8)
                this.ratio.eth = this.g.round2(1 / o.usdt_exchange * this.getCoinRatio(res.data.caches, 'ethusdt').close, 8)
                this.ratio.btc = this.g.round2(1 / o.usdt_exchange * this.getCoinRatio(res.data.caches, 'btcusdt').close, 8)
            }).fail(err => {
                this.loading = false
                this.$message.error([url, err.statusText])
            })
        },
        init() {
            const coin_name = this.g.getQueryVariable('name')
            if (!coin_name) {
                this.$message.error("没有这个平台币信息 : " + coin_name)
                return;
            }
            this.symbol = coin_name
            this.initCurrencyPlatformData()
        },
    },
    mounted() {
        this.init();
    },
};
</script>


<style scoped>
.startup {
    background-color: #232c48;
    font-family: "Courier New", Courier, monospace;
}
.bg-body {
    margin-top: 30px;
    padding: 10px;
    background-color: #161f37;
}
.mt30 {
    margin-top: 30px;
}
.starting-project {
    padding: 10px;
    color: #eeeeee;
    background-color: #161f37;
}
.t-w {
    color: #dddddd;
}
.t-weight {
    font-weight: 300;
}
.m5 {
    padding: 5px;
}
.img-r {
    border-radius: 5px;
}
.t1 {
    font-size: 2rem;
    font-weight: 300;
}
.t2 {
    font-size: 1rem;
    font-weight: 300;
}
.ant-card {
    color: #fff;
    background-color: #001529;
}
</style>